const ProjectWrapper = styled.section`
  position: relative;
`;
const ProjectUl = styled.ul`
  width: 400%;
  height: 100vh;
  display: flex;
  flex-wrap: nowrap;
  overflow: hidden;
  @media only screen and (max-width: 1024px) {
    width: 100%;
    height: 100%;
    display: block;
  }
`;
const ProjectLi = styled.li`
  width: 100%;
  display: flex;
  padding: 8rem 2rem;
  color: #e0dfd5;
  @media only screen and (max-width: 1024px) {
    display: block;
  }
`;
const ProjectImg = styled.img`
  width: 100%;
  height: 100%;
`;
const ProjectImgBox = styled.div`
  display: flex;
  flex: 1 0 55%;
  border-radius: 1% 0 0 1%;
`;
const ProjectTextBox = styled.div`
  flex: 1 0 45%;
  border-radius: 0 1% 1% 0;
  font-family: "Gowun Dodum", sans-serif;
  font-size: 1rem;
  position: relative;
  display: flex;
  flex-direction: column;
  padding-left: 1.5rem;
  @media only screen and (max-width: 1024px) {
    padding-left: 0;
    padding-top: 1.5rem;
  }
`;
const Title = styled.h2`
  color: white;
  text-transform: uppercase;
`;
const Content = styled.p`
  padding-top: 1.5rem;
  color: #e0dfd5;
  @media only screen and (max-width: 1024px) {
    padding-top: 0rem;
  }
`;
const Link = styled.a`
  text-decoration: underline;
  color: #e0dfd5;
  &:hover {
    color: #00eb7f;
  }
`;
const Category = styled.h1`
  font-size: 2.2rem;
  position: absolute;
  top: 3rem;
  left: 2rem;
  color: #00eb7f;
`;
const Span = styled.span``;
const TextSmallBox = styled.div`
  ${(props) => (props.link ? "margin-top:auto;" : "")}
  @media only screen and (max-width: 1024px) {
    margin-top: 1rem;
  }
`;
const Project = (props) => {
  const ulRef = useRef(null);
  const listRef = useRef([]);

  const createLinetRefs = (el, index) => {
    listRef.current[index] = el;
  };

  useEffect(() => {
    const element = ulRef.current;
    const totalLength = listRef.current.length;
    ScrollTrigger.saveStyles([element, listRef.current]);
    ScrollTrigger.matchMedia({
      "(min-width: 1025px)": function () {
        gsap.to(listRef.current, {
          xPercent: -100 * (totalLength - 1),
          ease: "none",
          duration: 0.1,
          scrollTrigger: {
            trigger: element,
            pin: true,
            scrub: 1,
            snap: 1 / (totalLength - 1),
            end: () => "+=" + element.offsetWidth,
          },
        });
        listRef.current.map((el, index) => {
          gsap.from(el, {
            duration: 1,
            opacity: 0,
            y: 130,
            scrollTrigger: {
              trigger: element,
              start: "center bottom-=100",
              toggleActions: "play none none reverse",
            },
          });
          return null;
        });
      },
      "(max-width:1025px)": function () {
        listRef.current.map((el, index) => {
          gsap.from(el, {
            duration: 1,
            opacity: 0,
            y: 130,
            scrollTrigger: {
              trigger: el,
              start: "top top+=130",
              toggleActions: "play none none reverse",
            },
          });
          return null;
        });
      },
      all: function () {
        listRef.current.map((el, index) => {
          gsap.to("body", {
            scrollTrigger: {
              trigger: element,
              start: "top center+=100",
              toggleActions: "play none none reverse",
            },
            backgroundColor: "#222831",
          });
          return null;
        });
        listRef.current.map((el, index) => {
          gsap.fromTo(
            ".socialBox",
            { display: "none" },
            {
              scrollTrigger: {
                trigger: element,
                start: "top center+=100",
                toggleActions: "play none none reverse",
              },
              duration: 0.1,
              display: "block",
            }
          );
          return null;
        });
      },
    });
  }, [ulRef, listRef]);

  return (
    <Element name="project">
      <ProjectWrapper>
        <ProjectUl ref={ulRef} className="itemBox">
          <Category>PROJECT</Category>
          {data.map((item, index) => {
            return (
              <ProjectLi
                ref={(e) => createLinetRefs(e, index)}
                className="project1"
                key={index}
              >
                <ProjectImgBox>
                  <ProjectImg src={item.img} />
                </ProjectImgBox>
                <ProjectTextBox>
                  <Title>{item.title}</Title>
                  <Content>{item.content}</Content>
                  <TextSmallBox link>
                    <Span>Link: </Span>
                    <Link href={item.link}>{item.link}</Link>
                  </TextSmallBox>
                  <TextSmallBox>
                    <Span>Source Link: </Span>
                    <Link href={item.sourceLink}>{item.sourceLink}</Link>
                  </TextSmallBox>
                </ProjectTextBox>
              </ProjectLi>
            );
          })}
        </ProjectUl>
      </ProjectWrapper>
    </Element>
  );
};

export default Project;
